.image-link {
  position: relative;
  display: inline-block;
  .image-container {
    display: none;
    position: absolute;
    top: -245px; /* 调整图片距离顶部的距离 */
    left: 0;
    width: 200px; /* 设置图片宽度 */
    height: 200px; /* 设置图片高度 */
    img {
      width: 100%; /* 确保图片宽度铺满容器 */
      height: 100%; /* 确保图片高度铺满容器 */
    }
  }
  &:hover .image-container {
    display: block;
  }
}

